@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,900;1,900&display=swap);
html,body{box-sizing:border-box}*,*:after,*:before{box-sizing:inherit}*{font-family:"montserrat", sans-serif;margin:0px}body{margin:0;padding:0;background-image:linear-gradient(125deg, #6a89cc, #b8e994)}.mainbox{align-items:center;color:#343434;display:flex;flex-direction:column;height:100vh;justify-content:center}.mainbox .number{align-items:center;display:flex;justify-content:center;position:relative}.mainbox .number h1{background:url(/static/media/bg.c1194249.jpg) center no-repeat;font-size:100px;font-weight:900;margin:0;padding:12px;-webkit-text-fill-color:transparent;-webkit-background-clip:text}.mainbox .number h1:nth-child(2){color:#ced6e0}.mainbox .number img{position:absolute}.mainbox .number .App-logo{height:75px;pointer-events:none}.mainbox .msg{align-items:center;display:flex;flex-direction:column;justify-content:center;margin:5px;padding:5px}.mainbox .msg a{border:solid 2px #e55039;background-color:#e55039;border-radius:5px;color:#ffffff;display:inline-block;font-size:15px;font-weight:bold;padding:12px 24px;text-decoration:none;text-transform:uppercase;transition:background-color .8s ease}.mainbox .msg a:hover{background-color:#ffffff;border:solid 2px #e55039;color:#e55039}.mainbox .msg p{font-size:16px;padding-bottom:30px}@media only screen and (min-width: 768px){.mainbox .number h1{font-size:130px}.mainbox .number .App-logo{height:100px;pointer-events:none}.mainbox .msg p{font-size:18px}}@media only screen and (min-width: 1024px){.mainbox .number h1{font-size:160px}.mainbox .number .App-logo{height:125px;pointer-events:none}.mainbox .msg p{font-size:20px}}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 10s linear;animation:App-logo-spin infinite 10s linear}}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

